* {
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
  background-color:rgba(23, 27, 142, 0.5)
}

a {
  text-decoration: none;
}

p {
  margin: 0;
}

.search-form {
  width: 100%;
  display: flex;
  justify-content: center;
  padding-top: 30px;
  padding-bottom: 30px;
  background-color: mediumblue;
}

.search-form input {
  width: 20%;
  border-radius: 5px 0 0 5px;
  border: none;
  outline: none;
  padding: 8px 10px;
}

.search-form button {
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  background-color: #fff;
  border-radius: 0 5px 5px 0;
  padding: 8px 20px;
  cursor: pointer;
}

.search-form button:hover {
  background-color: rgba(224, 224, 224, 0.5);
}

.load-more {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  border: none;
  background-color: #fff;
  border-radius: 5px 5px;
  padding: 8px 20px;
  cursor: pointer;
}

.load-more button:hover {
  background-color: rgba(224, 224, 224, 0.5);
}

.load-more-hidden {
  display: none;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 20px;
}

.gallery,
.info {
  display: flex;
  flex-wrap: wrap;
}

.gallery {
  margin-left: -20px;
  padding-top: 30px;
  justify-content: center;
}

.gallery-link {
  margin-top: 20px;
  margin-left: 20px;
  min-width: 320px;
  flex-basis: calc((100% - 80px) / 4);
  border: none;
  border-radius: 10px;
  overflow: hidden;
  transition: all 0.3s;
}

.gallery-link:hover,
.gallery-link:focus {
  transform: scale(1.05);
}

.photo-card {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.img-item {
  display: block;
  height: 30vh;
  -o-object-fit: cover;
  object-fit: cover;
}

.info {
  padding: 10px;
  justify-content: space-around;
}

.info-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: rgb(255, 255, 255);
  font-size: smaller;
}

.info-item:not(:first-child) {
  margin-left: 5px;
}